// Function to determine available actions based on status
const getAvailableActions = (status: ItemStatus, isFlagged?: boolean): string[] => {
  let actions: string[] = [];
  switch (status) {
    case 'Pending Delivery':
      actions = ['Mark as Delivered', 'Report Issue', 'View Details', 'Edit Item', 'Add Note']; break;
    case 'Delivered':
      actions = ['Archive', 'Report Issue', 'View Details', 'Edit Item', 'Add Note']; break;
    case 'Issue': // Issue-specific actions
      actions = ['Resolve Issue', 'Add Issue Update', 'Archive', 'View Details', 'Edit Item', 'Add Note']; break;
    case 'Late': 
      actions = ['Mark as Delivered', 'Report Issue', 'View Details', 'Edit Item', 'Add Note']; break;
    case 'Archived':
      actions = ['View Details']; break;
    default:
      actions = ['View Details']; break;
  }
  if (status !== 'Archived') {
    actions.unshift(isFlagged ? 'Unflag Item' : 'Flag Item');
  }
  return actions;
};

// Simple Action Menu Component
interface ActionMenuProps {
  itemId: string;
  actions: string[];
  onActionSelected: (itemId: string, action: string) => void;
  onClose: () => void;
}

const ActionMenu: React.FC<ActionMenuProps> = ({ itemId, actions, onActionSelected, onClose }) => {
  const menuRef = useRef<HTMLDivElement>(null);

  // Close menu if clicked outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        onClose();
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [onClose]);

  return (
    <div ref={menuRef} style={{
      position: 'absolute',
      backgroundColor: 'white',
      border: '1px solid #ccc',
      borderRadius: '4px',
      boxShadow: '0 2px 5px rgba(0,0,0,0.15)',
      zIndex: 10, // Ensure it appears above other elements
      minWidth: '150px',
      marginTop: '2px' // Small gap below the button
    }}>
      {actions.map(action => (
        <button
          key={action}
          onClick={() => onActionSelected(itemId, action)}
          style={{
            display: 'block',
            width: '100%',
            padding: '8px 12px',
            border: 'none',
            background: 'none',
            textAlign: 'left',
            cursor: 'pointer'
          }}
          onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = '#f0f0f0')}
          onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = 'transparent')}
        >
          {action}
        </button>
      ))}
    </div>
  );
};

